<template>
    <div>
        <div class="helptop">
            <img src="/static/img/aa.jpg" alt="">
            <h1></h1>
            <input type="text" placeholder="">
        </div>
        <div class="helpcontent">
            <div class="helpcontenttitle">
                <span></span><span></span>
            </div>
            <div class="helplogin">
                <h1></h1>
                <div class="loginsearch">
                    <router-link tag="input" type="button" value="" class="login" to="/login"></router-link>
                    <router-link tag="input" type="button" value="" class="register" to="/register"></router-link>
                </div>
            </div>
            <help-list :key="index" v-for="(i,index) in data.help1" :data="i"></help-list>
        </div>
    </div>
</template>
<script>
import data from "../components/help/data/help.json"
import helpList from "../components/help/helpList"
export default {
    data(){
        return {
            data
        }
    },
    components:{
        helpList
    }
}
</script>
<style>
@import "../assets/css/help.css"
</style>



